import React, { useState, useEffect } from "react";
import DataGrid from "../../containers/DataGrid";
import ReactHtmlParser from "react-html-parser";
import { Cluster } from "grommet-icons";
import Parse from "../../library/parse";
import { getStoreByRole } from "../../library/store";

import UserContext from "../../context/UserContext";
import { navigate } from "gatsby";
const configureCol = [
  {
    property: "name",
    headerText: "Name",
  },
  {
    property: "description",
    headerText: "Info",
    type: "Textarea",
    render: (data) => <> {ReactHtmlParser(data.description)} </>,
  },
  {
    property: "whatsapp",
    headerText: "Whatsapp",
    render: (data) => <> {ReactHtmlParser(data.whatsapp)} </>,
  },
  {
    property: "objectId",
    headerText: "Product",
    render: (data) => (
      <>
        <Cluster
          onClick={() => {
            navigate(`/account/store/manageproduct2/${data.objectId}`);
          }}
        />
      </>
    ),
  },
];

const App = () => {
  const _userContext = React.useContext(UserContext);
  const [datasource, setDatasource] = useState([]);

  useEffect(() => {
    const storeRoles =
      (_userContext &&
        _userContext.roleObjects
          .filter((ro) => ro.get("name").includes("Store-"))
          .map((ro) => ro.id)) ||
      [];
    if (storeRoles && storeRoles.length) {
      getStoreByRole(storeRoles, 999).then((ds) => {
        setDatasource(ds);
      });
    }
  }, [_userContext]);
  return (
    <>
      <DataGrid
        width="100%"
        objectClass="Store"
        columns={configureCol}
        query={(q) => q}
        datasource={datasource}
        afterAction={() => _userContext.refresh()}
      />
    </>
  );
};
export default App;
